p{
    text-align: center;
}
.login__container{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: 350px;
    padding: 40px 0;
    margin: 100px auto;
}

.login__container {
    img {
        width: 45%;
        margin: 40px 0;
        cursor: pointer;
        height: auto;
        padding: 8px 10px;
        border-radius: 35px;
        box-shadow: none;
        transition: box-shadow .3s ease;
        &:hover{
            box-shadow: 0px 0px 36px -17px rgba(15,15,15,1);
        }
    }
        form{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center
        }
    
        p{
            margin: 0px 10px;
        }
    }




.login__button-disable{
    padding: 10px;
    border: none;
    border-radius: 5px;
    background: rgba(255, 192, 203, 0.527);
    color: rgb(78, 78, 78);
    width: 350px;
    outline: none;
}

.login__button {
    padding: 10px;
    border: none;
    border-radius: 5px;
    background: pink;
    cursor: pointer;
    color: rgb(78, 78, 78);
    width: 350px;
    outline: none;
}


.login__span{
    color: pink;
}

@media screen and (max-width: 563px) {

    .login__button-disable,
    .login__button{
        width: 290px;
    }


}

